/* ******************* START OF GENERAL CSS ******************* */
/* background for all pages. */
.background {
  background: linear-gradient(
    0deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(73, 191, 199, 1) 0%,
    rgba(139, 141, 181, 1) 100%
  );
  overflow: auto;
  z-index: 0;
  display: flex;
  justify-content: center;
}
/* container for all pages' white background. */
.whiteBackground {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  background-color: white;
  text-align: center;
  width: 80vw;
}
/* container for all pages' content. Not including buttons. */
.contentContainer {
  display: flex;
  flex-grow: 1;
  padding: 2% 5%;
  margin: 10% 0 0 0;
  text-align: center;
  justify-content: space-around;
  flex-wrap: wrap;
  user-select: none; /* supported by Chrome and Opera */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
}

/* background style of contentBoxes, when selected. */
.selected {
  outline: none;
  border-color: #9ecaed;
  color: white;
  font-weight: bold;
  background: rgb(2, 0, 36);
  background: linear-gradient(
    0deg,
    rgba(73, 191, 199, 100%) 0%,
    rgba(139, 141, 181, 100%) 100%
  );
}

/* Cursor type for all boxes, that are selectable. */
.cursor {
  cursor: pointer;
}

/* background stripe for h1s in the color of background-class on all pages */
.stripe {
  background-color: #8492b6;
  color: white;
}

/* radius of additional selection options. Eg: Motionsensor => indoor / outdoor. */
.addSelectRadius {
  border-radius: 5px;
  padding: 1vmin 2vmin;
  margin-bottom: 5%;
}
/* ******************* END OF GENERAL CSS ******************* */

/* ******************* START OF BUTTON CSS ******************* */

/* button-container for single button on page - eg Start, Confirm.*/
.btnSingle {
  display: flex;
  flex-grow: 1;
  justify-content: center;
  align-items: flex-end;
  margin-bottom: 3%;
}

/* button-container for multiple buttons on page - eg Lights, Heating.*/
.btnMultiple {
  display: flex;
  flex-grow: 1;
  justify-content: space-between;
  align-items: flex-end;
  margin: 3% 5%;
}

/* button general style */
.btn:hover {
  background: linear-gradient(to bottom, #69a6be 5%, #4dbbc5 100%);
  background-color: #69a6be;
}
.btn:active {
  position: relative;
  top: 0.1rem;
}
/* ******************* END OF BUTTON CSS ******************* */

/* ******************* START OF CHECKMARK CSS ON START PAGE ******************* */
.checkmarkDiv {
  display: flex;
  justify-content: center;
  margin-top: -2%;
}
.checkmark {
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.checkmark li:before {
  content: 'âœ“';
  color: #2cba6c;
  font-size: 2.5rem;
}
/* ******************* END OF CHECKMARK CSS ON START PAGE ******************* */

/***** UNORDERED ****/
@media (max-width: 850px) {
  .contentBox {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-bottom: 2%;
    height: 30vmin;
    width: 30vmin;
    border-radius: 10%;
    font-size: 2.5vmin;
    box-shadow: 2px 5px 10px 5px rgba(2, 0, 36, 30%);
  }

  .contentBox > img {
    margin-top: 10%;
    height: 15vmin;
    width: 15vmin;
  }

  .contentBox > div {
    margin-top: 10%;
    padding: 0 10px;
  }

  .btn {
    background: linear-gradient(to bottom, #8a8db5 5%, #7a99b9 100%);
    background-color: #8a8db5;
    border: none;
    border-radius: 0.6rem;
    display: inline-block;
    cursor: pointer;
    color: #ffffff;
    font-family: Arial;
    font-size: 4vmin;
    font-weight: bold;
    padding: 3vmin 4vmin;
    text-decoration: none;
  }

  .addSelectContainer {
    display: flex;
    flex-direction: column;
    font-size: 3vmin;
    cursor: pointer;
  }
}

@media (min-width: 851px) {
  .contentBox {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-bottom: 2%;
    height: 20vmin;
    width: 20vmin;
    border-radius: 10%;
    font-size: 1.5vmin;
    box-shadow: 2px 5px 10px 5px rgba(2, 0, 36, 30%);
  }

  .contentBox > img {
    margin-top: 10%;
    height: 10vmin;
    width: 10vmin;
  }

  .contentBox > div {
    margin-top: 10%;
    padding: 0 10px;
  }

  .btn {
    background: linear-gradient(to bottom, #8a8db5 5%, #7a99b9 100%);
    background-color: #8a8db5;
    border: none;
    border-radius: 0.6rem;
    display: inline-block;
    cursor: pointer;
    color: #ffffff;
    font-family: Arial;
    font-size: 1.3rem;
    font-weight: bold;
    padding: 0.9rem 1.8rem;
    text-decoration: none;
  }

  .addSelectContainer {
    display: flex;
    flex-direction: column;
    font-size: 2vmin;
    cursor: pointer;
  }
}
