/* ******************* START OF GENERAL CSS ******************* */
/* background for all pages. */
.background {
  background: linear-gradient(
    0deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(73, 191, 199, 1) 0%,
    rgba(139, 141, 181, 1) 100%
  );
  overflow: auto;
  z-index: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
/* container for all pages' white background. */
.whiteBackground {
  display: -webkit-flex;
  display: flex;
  min-height: 100vh;
  -webkit-flex-direction: column;
          flex-direction: column;
  background-color: white;
  text-align: center;
  width: 80vw;
}
/* container for all pages' content. Not including buttons. */
.contentContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  padding: 2% 5%;
  margin: 10% 0 0 0;
  text-align: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  user-select: none; /* supported by Chrome and Opera */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
}

/* background style of contentBoxes, when selected. */
.selected {
  outline: none;
  border-color: #9ecaed;
  color: white;
  font-weight: bold;
  background: rgb(2, 0, 36);
  background: linear-gradient(
    0deg,
    rgba(73, 191, 199, 100%) 0%,
    rgba(139, 141, 181, 100%) 100%
  );
}

/* Cursor type for all boxes, that are selectable. */
.cursor {
  cursor: pointer;
}

/* background stripe for h1s in the color of background-class on all pages */
.stripe {
  background-color: #8492b6;
  color: white;
}

/* radius of additional selection options. Eg: Motionsensor => indoor / outdoor. */
.addSelectRadius {
  border-radius: 5px;
  padding: 1vmin 2vmin;
  margin-bottom: 5%;
}
/* ******************* END OF GENERAL CSS ******************* */

