@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@200&display=swap');
body {
  margin: 0;
  font-family: 'Roboto Slab', serif, -apple-system, BlinkMacSystemFont,
    'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
    'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Roboto Slab', serif, source-code-pro, Menlo, Monaco, Consolas,
    'Courier New', monospace;
}
